import React from "react";
import LazyHero from "react-lazy-hero";

const Hero = () => {
  return (
    <div>
      <LazyHero imageSrc="https://unsplash.it/2000/1000">
        <h1>Generic Startup Hype Headline</h1>
      </LazyHero>

      {/* ... */}
    </div>
  );
};
export default Hero;



